import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { map } from 'rxjs/operators';
import { DataService } from '../data/data.service';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-list',
  template: `
    <app-nav-header></app-nav-header>
    <p *ngFor='let item of items'>
      {{ item.dTITLE | uppercase }}
    </p>
  `,
  styles: [
  ]
})
export class ListComponent implements OnInit {

  items: any;
  url: Observable<string>;
  baseUrl: string;

  constructor( private ds: DataService, private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.url = this.route.url.pipe(map(segments => segments.join(' / ')));
    this.url.subscribe(u => {
      this.baseUrl = u;
      return u;
    });
    console.log('url:', this.url, this.baseUrl);
    this.ds.getData(this.baseUrl).subscribe(data => {
      this.items = data;
      console.log('items:', typeof this.items, this.items);
    });
  }

}
