import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  private url = 'http://littlechick-design.com/CLIENT/DA-backend/';
  private endParam = '.php?type=json';

  constructor(private http: HttpClient) { }

  getData(type: string): Observable<object[]>{
    const tmp = this.http.get<object[]>( this.url + type + this.endParam).pipe(map(arr => {
      return arr.map(obj => ({...obj, title: 'bob'}));
    }));
    console.log(typeof tmp, tmp);
    return tmp;
        // .success(function(data) {
        //     console.log(type + " fetched in home controller and stored to localstorage")
        //     window.localStorage.setItem("zapleti", JSON.stringify(data));
        // })
        // .error(function(response) {
        // });
  }

  getDrugs(): Observable<object[]> {
    const tmp: Observable<object[]> = this.http.get<object[]>(this.url + 'droge' + this.endParam);
    console.log('drfgettgf: ', typeof tmp);
    return tmp;
  }

}
